"use client";
import { motion, useScroll, useTransform } from "framer-motion";
import { Children, useMemo, useRef } from "react";

export default function HorizontalScroll({ title, children }: { title?: string; children: React.ReactNode }) {
  const ref = useRef<HTMLDivElement>(null);
  const { scrollYProgress } = useScroll({ target: ref, offset: ["start start", "end end"] });

  const count = Children.count(children);
  const total = useMemo(() => Math.max(count, 1), [count]);
  const x = useTransform(scrollYProgress, [0, 1], [0, -((total - 1) * 100)]); // in vw units
  const xvw = useTransform(x, (v) => `${v}vw`);

  return (
    <section id="features" ref={ref} className="relative h-[200vh]">
      <div className="sticky top-0 h-screen flex flex-col">
        {title && (
          <div className="container-max pt-8">
            <h3 className="font-heading text-3xl md:text-4xl text-emerald">{title}</h3>
          </div>
        )}
        <div className="relative flex-1 overflow-hidden">
          <motion.div style={{ translateX: xvw }} className="flex h-full">
            {Children.map(children, (child, i) => (
              <div key={i} className="w-screen h-full p-8">
                {child}
              </div>
            ))}
          </motion.div>
        </div>
      </div>
    </section>
  );
}
