"use client";
import HeroReveal from "@/components/motion/HeroReveal";
import HorizontalScroll from "@/components/motion/HorizontalScroll";
import SplitText from "@/components/motion/SplitText";
import TiltCard from "@/components/ui/TiltCard";
import MagneticButton from "@/components/ui/MagneticButton";
import Clock from "@/components/app/Clock";
import GregorianDate from "@/components/app/GregorianDate";
import HijriDate from "@/components/app/HijriDate";
import PrayerTimes from "@/components/app/PrayerTimes";

export default function Page() {
  return (
    <main className="relative">
      <HeroReveal />

      <section className="container-max py-24">
        <div className="max-w-3xl mx-auto text-center">
          <SplitText
            as="h2"
            className="font-heading text-4xl md:text-5xl text-emerald"
            text="A sanctuary for the soul, crafted for your daily journey to peace."
          />
          <div className="mt-8 text-lg text-emerald/80 leading-relaxed">
            <SplitText
              as="p"
              className=""
              text="Find rhythm in remembrance. Build resilience through reflections. Grow together with a community that cares."
            />
          </div>
          <div className="mt-12 flex justify-center">
            <MagneticButton label="Start Your Journey" href="https://qalbymuslim.com" />
          </div>
        </div>
      </section>

      <HorizontalScroll title="Our Value to You">
        <div className="w-screen h-full flex items-center justify-center">
          <div className="container-max grid md:grid-cols-2 gap-8">
            <TiltCard title="Daily Reminders" subtitle="Gentle prompts to keep your heart engaged." />
            <TiltCard title="Community Connect" subtitle="Share, support, and grow with others." />
          </div>
        </div>
        <div className="w-screen h-full flex items-center justify-center">
          <div className="container-max grid md:grid-cols-2 gap-8">
            <TiltCard title="Guided Reflections" subtitle="Short meditations for calm and clarity." />
            <TiltCard title="Personalized Duas" subtitle="Intentional prayers aligned with your goals." />
          </div>
        </div>
      </HorizontalScroll>

      <section className="container-max py-24">
        <div className="max-w-4xl mx-auto text-center">
          <SplitText
            as="h3"
            className="font-heading text-3xl md:text-4xl text-emerald"
            text="About QalbyMuslim"
          />
          <div className="mt-6 text-emerald/85 leading-relaxed text-lg">
            <SplitText
              as="p"
              text="QalbyMuslim is a spiritual wellness platform designed to help you nurture presence, gratitude, and inner peace. Through daily reminders, reflective practices, and community support, you’ll gently realign your heart with what matters most."
            />
          </div>
        </div>
      </section>
      <section className="container-max py-24" id="today">
        <div className="mb-8 text-center">
          <h3 className="font-heading text-3xl md:text-4xl text-emerald">Today</h3>
          <p className="mt-2 text-emerald/80">Current time <Clock className="font-mono" /> · <GregorianDate /> · <HijriDate /></p>
        </div>
        <PrayerTimes />
      </section>
    </main>
  );
}
