"use client";
import { motion, useScroll, useTransform } from "framer-motion";
import { useRef } from "react";
import MagneticButton from "@/components/ui/MagneticButton";
import HeroDevice from "@/components/ui/HeroDevice";
import DownloadBadges from "@/components/ui/DownloadBadges";

export default function HeroReveal() {
  const ref = useRef<HTMLDivElement>(null);
  const { scrollYProgress } = useScroll({ target: ref, offset: ["start start", "end start"] });

  const scale = useTransform(scrollYProgress, [0, 0.6, 1], [1, 1.15, 1.25]);
  const yTop = useTransform(scrollYProgress, [0, 0.6, 1], [0, -40, -80]);
  const yBottom = useTransform(scrollYProgress, [0, 0.6, 1], [0, 40, 80]);
  const opacity = useTransform(scrollYProgress, [0, 0.2, 1], [1, 1, 0.85]);

  return (
    <section ref={ref} className="relative h-[160vh] bg-emerald-soft">
      <div className="sticky top-0 h-screen flex items-center">
        <div className="container-max">
          <div className="max-w-5xl">
            <motion.h1
              style={{ scale, opacity }}
              className="font-heading text-6xl md:text-7xl text-emerald leading-tight"
            >
              <motion.span style={{ y: yTop }} className="block">The Heart</motion.span>
              <motion.span style={{ y: yBottom }} className="block">of Peace</motion.span>
            </motion.h1>
            <p className="mt-6 text-emerald/80 max-w-2xl text-lg">Stay connected with prayer times, Qibla compass, Quran, and daily reminders — all in one place.</p>
            <div className="mt-10">
              <MagneticButton label="Explore QalbyMuslim" href="#features" />
            </div>
            <div className="mt-12 flex justify-center">
              <HeroDevice className="" />
            </div>
            <div className="mt-8 flex justify-center">
              <DownloadBadges />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
