"use client";
import { motion } from "framer-motion";
import { useEffect, useRef } from "react";
import Image from "next/image";

type Insets = { top?: string; bottom?: string; left?: string; right?: string };

export default function HeroDevice({
  videoSrc = "/assets/hero-video.mp4",
  posterSrc = "/assets/hero-poster.svg",
  frameSrc = "/assets/iphone-14-frame.png",
  insets = { top: "5%", bottom: "5%", left: "5%", right: "5%" },
  showFlare = true,
  className = "",
}: {
  videoSrc?: string;
  posterSrc?: string;
  frameSrc?: string;
  insets?: Insets;
  showFlare?: boolean;
  className?: string;
}) {
  const videoRef = useRef<HTMLVideoElement | null>(null);

  useEffect(() => {
    const video = videoRef.current;
    if (!video) return;
    const obs = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const p = video.play();
            if (p && typeof (p as any).catch === "function") {
              (p as any).catch(() => {});
            }
          } else {
            try {
              video.pause();
            } catch {}
          }
        });
      },
      { threshold: 0.25 }
    );
    obs.observe(video);
    return () => obs.disconnect();
  }, []);

  return (
    <motion.div
      className={`relative mx-auto ${className}`}
      initial={{ opacity: 0, y: 24, scale: 0.98 }}
      whileInView={{ opacity: 1, y: 0, scale: 1 }}
      viewport={{ once: true, margin: "-20%" }}
      transition={{ duration: 0.8, ease: "easeOut" }}
      animate={{ y: [0, -8, 0] }}
      style={{
        width: "min(360px, 90vw)",
        aspectRatio: "9 / 19.5",
      }}
    >
      {/* Screen area */}
      <div
        className="absolute inset-0"
        style={{
          top: insets.top,
          bottom: insets.bottom,
          left: insets.left,
          right: insets.right,
        }}
      >
        <video
          ref={videoRef}
          className="h-full w-full rounded-[28px] object-cover shadow-glass"
          muted
          playsInline
          autoPlay
          loop
          preload="metadata"
          src={videoSrc}
          poster={posterSrc}
        />
      </div>

      {/* Flare overlay */}
      {showFlare && (
        <motion.div
          className="absolute inset-0 rounded-[36px]"
          style={{
            background:
              "radial-gradient(120px 60px at 50% 15%, rgba(212,175,55,0.16), transparent)",
          }}
          initial={{ opacity: 0 }}
          animate={{ opacity: [0.0, 0.6, 0.0] }}
          transition={{ duration: 2.0, ease: "easeInOut", repeat: Infinity, repeatDelay: 1.0 }}
        />
      )}

      {/* Frame image */}
      <Image
        src={frameSrc}
        alt="iPhone 14 frame"
        fill
        priority
        className="absolute inset-0 h-full w-full select-none pointer-events-none"
      />
    </motion.div>
  );
}
